	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Manchmal haben Frauen","date":"04 Mar 2001","slug":"/2001-03-04-manchmal-haben-frauen/","tags":"Y2001"},"html":"<p>Today's an off day as the team has a chance to recuperate and prepare for the tough week ahead.</p>\n<p>Yesterday Christopher and I spent our day upgrading PCs. I picked up a 900 MHz Athlon for myself, Mama gets my old 500 MHz Athlon and more RAM, and a CD writer. She's become interested in music from Napster lately, and her company car has a CD player. So now she can burn her own music CDs.</p>"},"next":{"fields":{"title":"U Don't Have 2 B Beautiful","date":"03 Mar 2001","slug":"/2001-03-03-u-dont-have-2-b-beautiful/"}},"previous":{"fields":{"title":"Hall of Fame","date":"05 Mar 2001","slug":"/2001-03-05-hall-of-fame/"}}},{"node":{"fields":{"title":"U Don't Have 2 B Beautiful","date":"03 Mar 2001","slug":"/2001-03-03-u-dont-have-2-b-beautiful/","tags":"Y2001"},"html":"<p>We've decided that Christopher is eating too much. It's not just that I've been calling him the 'bottomless pit' the past few weeks. A newborn baby needs to be fed whenever she's hungry, and it's very easy to soothe a crying spell with a cracker or a banana. Socially, Christopher always wants to eat along with the adults, even if he's just had his own meal. At 14 months it's time to start being a little more selective with feeding. So it's time that he eats his meals along with us, not before, and no more snacks to soothe every complaint. Today (a work day for Mama) was the first day of the new strategy. Christopher didn't like having to wait for us at breakfast this morning, but otherwise it was a smooth day for us men at home. </p>\n<p>This coming week will be another tough one. Mama has 3 days of meetings in Munich. At least this time I'm healthy, unlike the first 3 day stretch last month.</p>\n<p>FAZ: <a href=\"http://www.faz.com/IN/INtemplates/eFAZ/docmain.asp?rub=&#123;B1311FCC-FBFB-11D2-B228-00105A9CAF88&#125;&doc=&#123;8159E318-0E35-11D5-A3B3-009027BA22E4&#125;\">Court Allows Kohl to Pay Fine And End Donations Probe</a>. In the US, this would be a plea bargain and an admission of guilt. Here this seems to be a proclamation of innocence.</p>"},"next":{"fields":{"title":"Hello Everybody","date":"26 Feb 2001","slug":"/2001-02-26-hello-everybody/"}},"previous":{"fields":{"title":"Manchmal haben Frauen","date":"04 Mar 2001","slug":"/2001-03-04-manchmal-haben-frauen/"}}},{"node":{"fields":{"title":"Hello Everybody","date":"26 Feb 2001","slug":"/2001-02-26-hello-everybody/","tags":"Y2001"},"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2001/02/WinniePooh.jpg\" height=\"262\" width=\"350\" border=\"0\" alt=\"Winnie Pooh: \" /></p>\n<p><i>Dad sure writes a lot about me. But he hasn't posted any pictures lately. I finally talked Mom into posting one of my latest pictures.<br />\nHere I'm sitting in my favorite leather chair and catching up on Winnie Pooh's adventures. The book is a gift from my Uncle Steve and Aunt Shannon. They must be having fun shoveling snow in Minnesnowta! Just wanted to say Hi to everybody out there in the Weblog Community!</i></p>"},"next":{"fields":{"title":"Long for your Kiss","date":"24 Feb 2001","slug":"/2001-02-24-long-for-your-kiss/"}},"previous":{"fields":{"title":"U Don't Have 2 B Beautiful","date":"03 Mar 2001","slug":"/2001-03-03-u-dont-have-2-b-beautiful/"}}}],"pathPrefix":"","first":false,"last":false,"index":1017,"pageCount":1101}}

